body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html, body, .menurow {
  height: 100% !important; }

body {
  background-color: #ffffff; }

.toolBarContainer {
  max-width: 180vh !important;
  margin: auto 0; }

.MuiInputBase-input {
  font: inherit;
  color: #FCFCFC !important; }

.MuiAppBar-root {
  background-color: white !important; }

.MuiToolbar-regular {
  background-color: #212121;
  padding-bottom: 20px !important; }

.MuiMenu-list {
  background-color: #212121;
  color: #a4a4a4; }

.MuiTab-root {
  font-family: "Rajdhani", "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: bolder;
  font-size: 1.8rem !important;
  -webkit-transition: all .5s ease;
  transition: all .5s ease; }

.MuiTab-root:hover {
  box-shadow: 1px 1px 2px #000000, 2px 2px 0px rgba(0, 0, 0, 0.1);
  -webkit-transform: scale(1.1);
  /* IE 9 */
  transform: scale(1.1); }

.containerGrid {
  max-width: 180vh;
  padding: 0;
  margin: 0 auto !important; }

.boxcontent {
  display: flex;
  justify-content: center; }

@media screen and (max-width: 600px) {
  .MuiAppBar-positionSticky {
    top: 0;
    left: auto;
    right: 0;
    position: static !important; }
  .containerGrid {
    margin-top: 30px !important; }
  .myCard {
    padding: 0px !important; } }

.myCard {
  padding: 50px; }

.box {
  height: 100%; }

.menuitem {
  text-align: center;
  margin: auto 0;
  align-self: center; }

@media screen and (max-width: 600px) {
  .menuitem {
    margin-top: 20px !important;
    margin-bottom: -20px !important;
    text-align: center;
    height: 50px !important; }
  .box {
    padding: 20px; } }

.myicon {
  margin: 0 auto !important; }

i {
  color: #777777; }

.myem {
  color: #9B8449; }

.mySelect {
  background-color: #9B8449; }

.MuiSvgIcon-root {
  color: #9B8449; }

.img-fluid {
  max-width: 50%;
  max-height: 50%; }

h1 {
  font: 300 50px/1.3 'Lobster Two', Helvetica, sans-serif;
  color: #777777; }

h2 {
  font: 300 30px/1.3 'Lobster Two', Helvetica, sans-serif;
  color: #dadada;
  text-shadow: 1px 1px 0px #000000, 4px 4px 0px rgba(0, 0, 0, 0.15); }

h3 {
  font: 300 20px/1.3 'Lobster Two', Helvetica, sans-serif;
  color: #777777; }

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #9B8449 !important;
  opacity: 1 !important;
  /* Firefox */ }

::-moz-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #9B8449 !important;
  opacity: 1 !important;
  /* Firefox */ }

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #9B8449 !important;
  opacity: 1 !important;
  /* Firefox */ }

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #9B8449 !important;
  opacity: 1 !important;
  /* Firefox */ }

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #9B8449 !important; }

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #9B8449 !important; }

